h1, h2, h3, h4 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
}

h1 {
  font-size: 60px;
  line-height: 60px;
}

h2 {
  font-size: 34px;
  line-height: 40px;
}

h3 {
  font-size: 28px;
  line-height: 34px;
}

h4 {
  font-size: 22px;
  line-height: 28px;
}

img {
  cursor: pointer;
}

p {
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  line-height: 28px;
}

.p-small {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 20px;
}

.link {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  cursor: pointer;
  text-decoration: none;
}

.link li {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
}

.link li:hover {
  color: #C5344E;
}

.link-small {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  text-decoration: none;
}

.text-color-primary {
  color: #6C3563;
}

.text-color-secondary {
  color: #C5344E;
}

.text-color-secondary:hover {
  color: #6C3563;
}

.text-color-darkgrey {
  color: #322E3B;
}

.text-color-white {
  color: #FFFFFF;
}

.button-primary {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: #FFFFFF;
  background-color: #C5344E;
  border: 0px;
  padding: 12px 26px;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.3s;
}

.button-primary:hover {
  background-color: #6C3563;
}

.no-underline-link {
  text-decoration: none;
}

/* --------------------- Responsive --------------------- */

@media screen and (max-width: 1280px) {
  h1 {
    font-size: 50px;
    line-height: 56px;
  }
  
  h2 {
    font-size: 30px;
    line-height: 36px;
  }
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 44px;
    line-height: 50px;
  }
}

@media screen and (max-width: 375px) {
  h1 {
    font-size: 44px;
    line-height: 50px;
  }

  h2 {
    font-size: 28px;
    line-height: 34px;
  }

  h3 {
    font-size: 24px;
    line-height: 30px;
  }
}